* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

.nav {
    position: fixed;
    top: 0;
    left: 0;
    background: rgb(127, 177, 156);;
    min-height: 80px;
    width: 100%;
    text-align: right;
    color: whitesmoke;
    font-size: 1.5em;
    font-weight: bold;
    z-index: 99;
}

.title{
    position: absolute;
    left: 0;
    margin-left: 5%;
    margin-top: 1%;
    color: whitesmoke;
}

.score {
    position: absolute;
    right: 0;
    margin-right: 5%;
    margin-top: 1%;
}

.message {
    position: absolute;
    left: 35%;
    margin-top: 1%;
    -webkit-animation: shake 0.5s;
            animation: shake 0.5s;
    /* text-align: center; */
}

.card {
  background: #fff;
  border-radius: 2px;
  height: 215px;
  margin: 1rem;
  position: relative;
  width: 210px;
  box-shadow: 0 2px 4px #999, 0 2px 4px #999;
}

.card > .imgContainer {
  height: 100%;
}
.card > .imgContainer > img {
  width: 100%;
  height: 100%;
}

.card:hover { 
  cursor: pointer;
  -webkit-transform: scale(1.02);
          transform: scale(1.02); 
}

.wrapper {
    background-color: gray;
    background-size: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    padding: 20px 10px;
    margin-bottom: 20px;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-content: flex-start;
            align-content: flex-start;
    overflow: auto;
}


.titleSection {
  background-size: cover;
  background-size: 100%;
  width: 100%;
  min-height: 150px;
  text-align: center;
  font-size: 3rem;
  color:whitesmoke;
  margin-bottom: 20px;
  padding-top: 20px;
  position: relative;
  top: 25px;
}

.footer {
    position: fixed;
    bottom: 0;
    left: 0;
    background: rgb(127, 177, 156);;
    min-height: 20px;
    width: 100%;
    text-align: center;
    color: whitesmoke;
    font-size: 12px;
    font-weight: bold;
    z-index: 99;
    padding-top: 8px;
}
